@import "../../styles/variables";
@import "../../styles/mixins";

.launch {
  .container {
    @include tablet-portrait {
      max-width: 100%;
    }
  }
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
    @include phone {
      flex-wrap: wrap;
      justify-content: flex-start;
      border-bottom: 1px solid #f4f4f4;
    }
  }

  &__wrapper {
    @include tablet-portrait {
      width: 100%;
      overflow-x: scroll;
    }
  }
  &__bottom {
    display: block;
    &_mod {
      display: none;
    }
  }
  &__details {
    font-size: 14px;
    line-height: 28px;
    font-weight: 400;
    margin-right: 20px;
    @include phone {
      max-width: 295px;
    }
  }
}

.page-link {
  &:hover {
    cursor: pointer;
  }

  &:focus {
    box-shadow: none;
  }
  @include phone {
    padding: 6px 10px;
  }
}

.pagination {
  margin: 20px 0;
  justify-content: center;
}

.page-item.active .page-link {
  background-color: #42a7fe;
  border-color: #42a7fe;
}

.hint {
  font-size: 14px;
  padding-top: 10px;
  border-top: 1px solid #f4f4f4;
  &_mod {
    img {
      width: 10px;
      height: 10px;
    }
  }
}
